'use strict';

/**
 * @class
 */
class LocalUserData {
    /**
     * @constructor
     */
    constructor() {
        /**
         * @type {string}
         */
        this.score = 0;

        /**
         * @type {!Array<Object>}
         */
        this.log = [];
    }
}

module.exports = LocalUserData;



