'use strict';

const content = require('./about.md');

const template = `
    <div class="wrap">
        <div class="text-block">
            ${content}
        </div>
    </div>               
`;

module.exports = {
    name: 'about',
    template: template,
    data: () => {
        return {};
    }
};



