'use strict';

const LocalUserData = require('./localUserData');

const STORAGE_KEY = 'score.fels.app.v2';

/**
 * @constructor
 */
function LocalUserDataProvider() {
    const self = this;

    /**
     * @param {LocalUserData} localUserData
     */
    this.save = function save(localUserData) {
        if (localStorage) {
            localStorage.setItem(STORAGE_KEY, JSON.stringify(localUserData));
        }
    };

    /**
     * @returns {!LocalUserData}
     */
    this.load = function load() {
        let userData = new LocalUserData();

        if (localStorage) {
            try {
                userData = JSON.parse(localStorage.getItem(STORAGE_KEY)) || {};
                userData = Object.assign(new LocalUserData(), userData);
            } catch (error) {
                console.warn('Cann not read or parse local user data.', {error});
            }
        }

        return userData;
    };

    /**
     * @param {function(LocalUserData):LocalUserData} updateCallback
     */
    this.update = function update(updateCallback) {
        let localUserData = self.load();
        updateCallback(localUserData);
        self.save(localUserData);
    };
}

module.exports = new LocalUserDataProvider();



