'use strict';

const template = `
    <div class="app-bar">
        <div class="wrap">
            <div class="column-block">
                <div class="column-block-column">
                    <div class="app-bar-box">
                       <app-menu class="panel"></app-menu>
                    </div>
                </div>
                <div class="column-block-column-fill">
                    <div class="app-bar-header">SCORE</div>
                </div>
                <div class="column-block-column">
                    <app-logo></app-logo>
                </div>
            </div>
        </div>
    </div>
`;

module.exports = {
    name: 'app-bar',
    template: template,
    data: () => {
        return {};
    }
};



