'use strict';

const aboutComponent = require('../routes/about');
const navigationState = require('../../states/navigationState');

const template = `<div v-on:click="openMenu" class="app-bar-icon menu-white-icon app-mode-active"></div>`;

module.exports = {
    name: 'app-menu',
    template: template,
    data: () => {
        return {};
    },
    methods: {
        openMenu: () => {
            navigationState.navigateTo(aboutComponent.name);
            console.log('Opening menu');
        }
    }
};



