'use strict';

/**
 * @class
 */
class ScoreLog {
    /**
     * @constructor
     * @param {number} value
     */
    constructor(value) {
        this.createdAt = new Date().toISOString();
        this.value = value;
    }
}

module.exports = ScoreLog;



