'use strict';

/* eslint-disable global-require */
/* eslint-disable no-unused-vars */

const initializeServiceWorker = require('./initializeServiceWorker');
const Vue = require('vue/dist/vue.js');
const navigationState = require('./lib/states/navigationState');
const components = require('./lib/components/componentRegistry');

initializeServiceWorker();

Object.keys(components).forEach(key => Vue.component(components[key].name, components[key]));

const template = `
    <div id="app" class="panel header-panel">
        <div class="header-panel-header">
            <app-bar></app-bar>
        </div>
        <div class="header-panel-body">
            <div class="scroll-panel" data-element="scrollContainer">
                <component v-bind:is="currentRouteComponent"></component>
            </div>
        </div>
    </div>
`;

const app = new Vue({
    el: '#app',
    template: template,
    data: {
        currentRoute: components.scorePage.name
    },
    computed: {
        currentRouteComponent: function() {
            return this.currentRoute;
        }
    }
});

navigationState.onNavigationChanged = route => {
    app.currentRoute = route;
};

console.log('Welcome to score.'); // eslint-disable-line no-console



