'use strict';

const scorePage = require('../routes/scorePage');
const navigationState = require('../../states/navigationState');

const template = `<div class="app-bar-icon" v-on:click="showScorePage">
    <img src="./icons/grade-climpup-app-icon-96.png" width="100%" height="100%" alt="ClimbUP Grade Logo"/>
</div>`;

module.exports = {
    name: 'app-logo',
    template: template,
    data: () => {
        return {};
    },
    methods: {
        showScorePage: () => {
            navigationState.navigateTo(scorePage.name);
        }
    }
};



