'use strict';

const defaultRouteComponent = require('../components/routes/scorePage');

function NavigationState() {
    /**
     * @type {!NavigationState}
     */
    let self = this;

    /**
     * @type {string}
     */
    let activeRoute = defaultRouteComponent.name;

    /**
     * @type {function()}
     */
    this.onNavigationChanged = () => {};

    /**
     * @returns {string}
     */
    this.getActiveRoute = function getActiveRoute() {
        return activeRoute;
    };

    /**
     * @param {string} route
     */
    this.navigateTo = function navigateTo(route) {
        activeRoute = route;
        self.onNavigationChanged(route);
    };

}

module.exports = new NavigationState();



