'use strict';

const ScoreLog = require('../../core/scoreLog');
const localUserDataProvider = require('../../store/localUserDataProvider');

const template = `
    <div class="wrap">
        <div class="score-page-score">{{score}}</div>
        <div class="score-page-buttons">
            <button class="score-page-button" v-on:click="addScore" data-value="50">50</button>
            <button class="score-page-button" v-on:click="addScore" data-value="50">100</button>
        </div>
        <div class="score-page-buttons">
            <button class="score-page-button" v-on:click="addScore" data-value="50">200</button>
            <button class="score-page-button" v-on:click="addScore" data-value="50">250</button>
        </div>
        <div class="score-page-buttons">
            <button class="score-page-button" v-on:click="addScore" data-value="50">500</button>
            <button class="score-page-button" v-on:click="undo">Undo</button>
        </div>
    </div>              
`;

const userData = localUserDataProvider.load();

const sumScoreLog = (total, item) => total + item.value;

module.exports = {
    name: 'score-page',
    template: template,
    data: () => userData,
    methods: {
        addScore: function(event) {
            const value = parseInt(event.target.getAttribute('data-value'), 10);

            userData.score += value;
            userData.log.push(new ScoreLog(value));

            localUserDataProvider.save(userData);

            const prettyUserData = JSON.parse(JSON.stringify(userData));
            console.warn('addScore', {event, prettyUserData});
        },
        undo: function() {
            userData.log.pop();
            userData.score = userData.log.reduce(sumScoreLog, 0);

            localUserDataProvider.save(userData);

            const prettyUserData = JSON.parse(JSON.stringify(userData));
            console.warn('undo', {prettyUserData});
        }
    }
};



